import React from 'react'
import Card from './Card'
import Timer from './Timer'
import Button from './UI/Button'
import './cardGroup.css'
import deleteIcon from './UI/icons/delete.png'


function GardGroup(props) {
    const timers = props.timers

    // Return a list of timer cards
    const timersList = timers.map((timer) => {
        return (
            <div key={timer.id} className={`card ${props.current === timer.id && 'current'}`}>
                <Card >                
                    <img className="deleteButton" onClick={props.delete} id={timer.id} src={deleteIcon} alt="delete" />
                    <h1>{timer.title}</h1>
                    <Timer time={timer.time} />
                </Card>
            </div>
        )
    })

    return (
        <div className="card-group">
            {timersList}
            {props.timers.length > 0 && <Button onClick={props.start}>{props.timerRunning ? 'stop' : 'start'}</Button>}
            <Button onClick={props.addTimer} >Add new</Button>

        </div>
    )
}

export default GardGroup
